.memo-app {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
}

.memo-header {
  margin-bottom: 30px;
  padding-bottom: 20px;
  border-bottom: 2px solid #e0e0e0;
}

.memo-header h1 {
  margin: 0 0 20px 0;
  color: #333;
  font-size: 2rem;
}

.memo-controls {
  display: flex;
  gap: 10px;
  align-items: center;
}

.search-input {
  flex: 1;
  padding: 12px 16px;
  border: 2px solid #e0e0e0;
  border-radius: 8px;
  font-size: 16px;
  transition: border-color 0.3s;
}

.search-input:focus {
  outline: none;
  border-color: #4CAF50;
}

.btn-create {
  padding: 12px 24px;
  background-color: #4CAF50;
  color: white;
  border: none;
  border-radius: 8px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: background-color 0.3s;
}

.btn-create:hover {
  background-color: #45a049;
}

.btn-create:active {
  transform: scale(0.98);
}

.memo-list {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.empty-state {
  text-align: center;
  padding: 60px 20px;
  color: #999;
  font-size: 18px;
}

@media (max-width: 600px) {
  .memo-app {
    padding: 15px;
  }

  .memo-header h1 {
    font-size: 1.5rem;
  }

  .memo-controls {
    flex-direction: column;
  }

  .search-input {
    width: 100%;
  }

  .btn-create {
    width: 100%;
  }
}

